
html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
}
#app {
  max-width: 660px;
  margin: 0 auto;
  height: 100%;
}
#app .document-title {
    background: #000;
    color: #ccc;
    font-family: 'microsoft yahei';
    font-size: 24px;
    text-align: center;
    line-height: 40px;
    margin: 0;
}
#app .vScrollbar1,
  #app .vScrollbar2,
  #app .vScrollbar3,
  #app .vScrollbar4,
  #app .vScrollbar5,
  #app .vScrollbar6 {
    max-width: 400px;
    height: 200px;
    border: 1px solid red;
    margin: auto;
}
#app .vScrollbar4 {
    background: #272822;
    color: #959595;
}



